@forward 'globals'; //name of folder
@forward 'util';
//Icons for LinkedIn, Mail, GitHub
.socials {
    display: flex;
    gap: 14px;
}

body {
    background-image: url(/public/assets/backgroundmobile.png);
    background-repeat: no-repeat;
    background-position: top;
    background-position-y: 60px;
}

header {
    .logo {
        display: none;
    }
    display: flex;
    align-items: center;
    background-color: var(--white);
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
    position: sticky;
    top: 0;
    padding: 22px;
    nav {
        display: none;
    }
}

main {
    display: flex;
    flex-direction: column;
    padding: 10px 0px 120px 0px;
    margin: auto; //So that it's centered in body
    gap: 20px;
    max-width: 18.875rem; //309px //beschr√§nkt den Inhalt
    .project-container {
        display: flex;
        flex-direction: column;
        gap: 108px;
        .project>* {
            margin-top: 25px;
        }
    }
}

.cite-container {
    text-align: center;
    .fancy-dots {
        display: inline-block;
        font-style: normal;
        font-size: 5rem;
        color: var(--light-brown);
        padding-bottom: 56px;
        line-height: 0.8;
    }
    p {
        font-style: italic;
        padding-bottom: 56px;
        font-weight: 500;
        span {
            font-weight: 300;
        }
    }
}

.portrait {
    width: 100%;
}

.section-one,
.section-two {
    h3 {
        margin: 28px 0px 28px 0px;
    }
}

footer {
    background-color: var(--white);
    box-shadow: 4px 0px 10px rgba(0, 0, 0, 0.25);
    padding: 33px 0px 33px 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 32px;
}

//714px
@media (min-width: 44.6em) {
    body {
        background-image: url(/public/assets/backgroundmedium.png);
    }
    main {
        max-width: 714px; //588px
        gap: 70px;
        padding: 40px 0px 120px 0px;
    }
    header {
        justify-content: space-between;
        nav {
            display: inline;
            font-size: 1.2rem;
            font-weight: 500;
            a {
                padding: 10px 20px;
                border: 2px solid var(--white); //so that it won't wobble while hovering
                border-radius: 84px;
                transition: 0.4s;
            }
            a+a {
                margin-left: 40px;
            }
            a:hover {
                padding: 10px 26px;
                border: 2px solid var(--black);
                border-radius: 84px;
            }
            .active {
                padding: 10px 26px;
                border: 2px solid var(--black);
                border-radius: 84px;
            }
        }
    }
    .portrait {
        width: 271px;
        height: 407px;
    }
    .section-one {
        display: flex;
        gap: 52px;
        .text {
            padding-right: 27px;
        }
    }
    .section-two {
        display: flex;
        gap: 52px;
        img {
            order: 2;
        }
        .text {
            padding-left: 27px;
            text-align: right;
        }
    }
    footer {
        flex-direction: row;
        justify-content: center;
        gap: 130px;
    }
}

//997px
@media (min-width: 62.3em) {
    body {
        background-image: url(/public/assets/backgroundlarge.png);
    }
    main {
        max-width: 714px; //588px
        gap: 70px;
        padding: 40px 0px 120px 0px;
    }
    header {
        .logo {
            display: inline;
            width: 54px;
        }
        justify-content: space-between; //so that other elements in header won't wobble while hovering
    }
    .portrait {
        width: 364px;
        height: 546px;
    }
    .section-one {
        .text {
            padding-right: 0px;
        }
    }
    .section-two {
        .text {
            padding-left: 0px;
            text-align: right;
        }
    }
}